<template>
    <div>
        <home-header></home-header>
        <home-swiper :swiper="swiper"></home-swiper>
        <home-icons :swipeItem="swipeItem"></home-icons>
        <home-like :like="likeList"></home-like>
    </div>
</template>

<script>
import axios from "axios"
import HomeHeader from "./components/Header"
import HomeSwiper from "./components/Swiper"
import HomeIcons from "./components/Icons"
import HomeLike from "./components/Like"
export default {
    name:"Home",
    data () {
        return {
            swipeItem:[],
            likeList:[],
            swiper:[]
        }
    },
    components: {
        HomeHeader,
        HomeSwiper,
        HomeIcons,
        HomeLike
    },
    methods:{
        getItemInfo() {
            axios.get("static/mock/index.json")
                .then(this.handleGetItemInfo)
        },
        handleGetItemInfo(res) {
            res = res.data
            if (res.ret && res.data) {
                const data = res.data
                this.swipeItem = data.swipeItem
                this.likeList = data.likeList
                this.swiper = data.swiper
            }
        }
    },
    mounted() {
        this.getItemInfo()
    }
}
</script>

<style>

</style>



