<template>
    <div class="icons">
        <div class="icon" v-for="item of swipeItem" :key="item.id">
            <div class="icon-img">
                <img class="item" :src="item.imgUrl">
            </div>
            <p class="icon-desc">{{item.itemTitle}}</p>
        </div>
    </div>
</template>

<script>
export default{
    name:"HomeIocns",
    props:{
        swipeItem:Array
    }
}
</script>

<style lang="stylus" scoped>
@import "~styles/varibles.styl"
    .icons
        overflow:hidden;
        height:0;
        padding-bottom:49.5%;
        display:flex;
        justify-content:space-around;
        flex-wrap:wrap
    
        .icon
            position: relative;
            overflow: hidden;
            float: left;
            width: 25%;
            height: 0;
            padding-bottom: 25%;

            .icon-img
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: .44rem;
                box-sizing: border-box;
                padding: .1rem;

        .item
            height:1.1rem;
            width:1.1rem;
            background-size:374px 130px;
        
        .icon-desc
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            height: .44rem;
            line-height: .44rem;
            text-align: center;
            color: #333;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
</style>





