<template>
    <div>
        <header>
            <div class="header-left">
                <span class="iconfont back-icon">&#xe61e;</span>
            </div>
            <div class="header-input">
                <span class="iconfont">&#xe627;</span>
                输入城市/经典/游玩主题
            </div>
            <router-link to="/city">
            <div class="header-right">
                {{this.city}}
                <span class="iconfont arrow-icon">&#xe64a;</span>
            </div>
            </router-link>
        </header>
    </div>
</template>
    
<script>
import { mapState } from 'vuex'
export default {
    name:"HomeHeader",
    computed:{
        ...mapState(['city'])
    }
}
</script>

<style lang="stylus" scoped>
    @import "~styles/varibles.styl"
    header
        background:$bgColor;
        height:$headerHeight;
        line-height:$headerHeight;
        display:flex;
        color:$lightColor;
        font-size:.28rem
        justify-content:space-around;
    
        .header-left
            width:.64rem;
            text-align:center;
        
            .back-icon
                font-size:.4rem;
        
        .header-input
            flex:1;
            margin:.12rem 0 .12rem .2rem;
            padding:0 .12rem 0 .12rem;
            background:$lightColor;
            border-radius:0.1rem;
            line-height :.64rem;
            height :.64rem;
            color:#ccc;
        
        .header-right
            min-width:1.04rem;
            padding:0 .1rem;
            text-align:center;
            color:$lightColor;
        
            .arrow-icon
                margin-left:-.04rem;
                font-size:.22rem;
</style>


