<template>
    <div class="like">
        <div class="like-List" >
            <h3 class="list-Title">
                <!-- <img src="@/images/like.png" alt=""> -->
                <span class="banner-icon">&#xe633;</span>
                猜你喜欢
            </h3>
            <router-link tag="div" 
                class="like-Item" 
                v-for="item of like" 
                :key="item.id" 
                :to="'/detail/' + item.id "
            >
                <div class="like-Img" >
                    <img :src="item.imgUrl" alt="">
                </div>
                <div class="like-Info">
                    <p class="item-Title">{{item.itemTitle}}</p>
                    <p class="price">
                        <span class="price-sym">￥</span>
                        {{item.price}}
                    </p>
                    <p class="address">{{item.address}}</p>
                    <p class="item-ellipsis">{{item.ellipsis}}</p>
                </div>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    name:"HomeLike",
    props:{
        like:Array
    }
}
</script>

<style lang="stylus" scoped>
    .like-List
        
    /* like-list左 */
        .list-Title
            font-weight: 500;
            font-size:.32rem;
            padding:.2rem;
            

        .like-Item
            padding:.2rem;
            overflow:hidder;
            height: 2.7rem;
            border-bottom:1px solid #eee;

            .like-Img
                float: left;
                display:inline-block;
                height: 2rem;
                width:2rem;
            
                img
                    width: 100%;
                    height: 100%;

            /* like-list 右 */
            .like-Info
                position: relative;
                width:63%;
                height:100%;
                overflow: hidden;
                float:left;
                display:inline-block;
                padding:.2rem 0 0 .2rem;
                
                p
                    line-height:.5rem;

                .item-Title
                    font-size:.32rem;
        
                .price
                    font-size:.4rem;
                    display:inline-block;
                    color:#ff8300;
        
                    .price-sym
                        font-size: .24rem;
                        margin-right:-.1rem;

                .address
                    font-size: .24rem;
                    float:right;

                .item-ellipsis
                    font-size: .24rem;
                    position:absolute;
                    bottom:.2rem;
                    color:#f55;
</style>




