<template>
    <div class="wrapper">
        <swiper :options="swiperOption">
            <!-- slides -->
            <swiper-slide class="swiper-slide" v-for="item of swiper" :key="item.id">
                <img :src="item.img" class="swiper-img">
            </swiper-slide>
            <!-- 显示分页 -->
            <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
export default {
    name:"HomeSwiper",
    props:{
        swiper:Array
    },
    data () {
        return {
            swiperOption:{
                pagination:".swiper-pagination",
                loop:true,
                autoplay:1500
            }
        }
    }
}
</script>

<style lang="stylus" scoped>
    .wrapper >>> .swiper-pagination-bullet-active
        background :#fff;

    .wrapper
        height:0;
        width:100%;
        overflow : hidden;
        padding-bottom:31.25%;
        background:#eee;

        .swiper-slide
            width:100%;
            height:120px;
    
            .swiper-img
                width:100%;
                height:100%;
</style>



